	module.exports = {"data":{"post":{"html":"<p>In a blatent attempt to influence the US Presidential election, the Michael Moore film Fahrenheit 9/11 <a title=\"Fahrenheit 9/11 im TV\" href=\"http://ameisendorf.de/?itemid=88\">will shown on television on Election Day</a>. German and Austrian televsion, that is (Pro7/Sat1 and ORF).</p>\n<p>Except that Germans and Austrians can't vote in American elections. And US expat voters will have had to have sent in their absentee ballots long before the film is aired. So what's the point?</p>\n<p>(Correction: the film will be shown on 1 November, not 2 November. The <a href=\"http://www.netzeitung.de/medien/309149.html\">Netzitung article</a> thorte cited was unclearly worded.)</p>","fields":{"title":"Fahrenheit 11/2","date":"14 Oct 2004","url":"https://papascott.de/2004-10-14-fahrenheit-112/"}}},"pathContext":{"slug":"/2004-10-14-fahrenheit-112/","prev":{"fields":{"title":"The Dark Monk","date":"13 Oct 2004","slug":"/2004-10-13-the-dark-monk/"}},"next":{"fields":{"title":"From My Spam Folder","date":"14 Oct 2004","slug":"/2004-10-14-from-my-spam-folder/"}}}}

